import { React, useState, useReducer,useEffect} from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router';
import { Link } from 'react-router-dom';

let ManageJob = () => {
    let [accounts, setAccounts]=useState([]);
    let [templates, setTemplates]=useState([]);
    let [file, setFile]=useState('')
    const [isFetchingData, setIsFetchingData] = useState(false);
    const goto = useNavigate();

    const template = async()=> {
         await axios.get(process.env.REACT_APP_API_URL +'/admin/settings')
         .then(response => {
            setAccounts(response.data.accounts);
            setTemplates(response.data.templates);
        }).catch(err => {
            console.log(err);
        });
    };

    useEffect(() => {
        template();
    }, []);

    // job.files = Math.ceil( ( job.total / job.template.items ) / job.template.pages );

    const [job, setFormInput] = useReducer(
        (state, newState) => ({ ...state, ...newState }),
        {
            accountId   : 1,
            batch       : '',
            template    : '',
            total       : 0,
            files       : 0,
            background  : 1,
            data_file   : '',
            status      : 1
        }
    );

    const handleInput = evt => {
        const name = evt.target.name;
        const newValue = evt.target.value;
        setFormInput({ [name] : newValue });
    };

    // const onFileChange = this.onFileChange.bind(this);
    // const addjob = onSubmit.bind();

    const onFileChange = (e) => {
        setFile( e.target.files[0])
    }  

    const addjob = (evt) => {
        evt.preventDefault();

        // if( job.template ){
        //     var temdx = templates.findIndex( s => s.metaId === job.template  );
        //     if( temdx !== -1 ) {
        //         console.log('templates',templates);
        //     }
        // }
        // return;
        setIsFetchingData(true)
        const formData = new FormData();
        formData.append('file', file);
        formData.append('accountId', job.accountId);
        formData.append('status', job.status);
        formData.append('template', job.template);
        formData.append('batch', job.batch);
        formData.append('background', job.background);
        axios.post( process.env.REACT_APP_API_URL +'/job',formData )
        .then((response) => {
            alert(response.data.message);
            setIsFetchingData(false);
            if( response.data.status === 'success' ) {
                goto('/admin/jobs');
            }
        }).catch((err) => {
            console.log(err);
        });
    }

    return(
    <div className="container">
        <div className="row">
            <div className="col-12">
                <h3 className="text-primary">Add a Job</h3>
                <form onSubmit={addjob} encType="multipart/form-data">
                    <div className='row'>
                        <div className='col-md-4 mb-3'>
                            <label htmlFor='batch'> Batch <span className='text-danger'>*</span></label>
                            <input type='text' className='form-control'  defaultValue={job.batch} name='batch' required onChange={handleInput} />
                        </div>

                        <div className='col-md-4 mb-3'>
                            <label htmlFor='name'> Account <span className='text-danger'>*</span></label>
                            <select className='form-select' value={job.accountId} required  name='accountId' onChange={handleInput} >
                                <option>--Select Account--</option>
                                {accounts.map(row=>{
                                    return(
                                        <option key={row.metaId} value={row.metaId}>{row.meta_key || ''}</option>
                                    )
                                })}
                            </select>
                        </div>
                    
                        <div className='col-md-4 mb-3'>
                            <label htmlFor='template'>Template <span className='text-danger'>*</span></label>
                            <select className='form-select' value={job.template} name='template' required onChange={handleInput}>
                                <option>--Select Template--</option>
                                {templates.map(row=>{
                                    return(
                                        <option  key={row.metaId}  value={row.metaId}>{row.meta_key || ''}</option>
                                    )
                                })}
                            </select>
                        </div>

                        <div className='col-md-4 mb-3'>
                            <label htmlFor='data_file'> Upload file<span className='text-danger'>*</span> </label>
                            <input type='file' onChange={onFileChange} accept=".csv"  className='form-control' name='data_file' required defaultValue={job.data_file}  />
                        </div>

                        <div className='col-md-4 mb-3'>
                            <label htmlFor='background'> Background <span className='text-danger'>*</span> </label><br/>
                            <div className="form-check form-check-inline mt-2">
                                <input className="form-check-input" type="radio" name="background" id="background1" value="1" onChange={handleInput} defaultChecked={job.background}/>
                                <label className="form-check-label" htmlFor="background1">Yes</label>
                            </div>
                            <div className="form-check form-check-inline">
                                <input className="form-check-input" type="radio" name="background" id="background2" value="0" onChange={handleInput} />
                                <label className="form-check-label" htmlFor="background2">No</label>
                            </div>
                        </div>

                        <div className='col-md-4 mb-3'>
                            <label htmlFor='status'> Status <span className='text-danger'>*</span> </label><br/>
                            <div className="form-check form-check-inline mt-2">
                                <input className="form-check-input" type="radio" name="status" id="status0" value="0" onChange={handleInput}/>
                                <label className="form-check-label" htmlFor="status0">Draft</label>
                            </div>
                            <div className="form-check form-check-inline">
                                <input className="form-check-input" type="radio" name="status" id="status1" value="1" onChange={handleInput} defaultChecked={job.status} />
                                <label className="form-check-label" htmlFor="status1">Print</label>
                            </div>
                        </div>
                        
                        <div className="col-12 mt-3">
                            {/* <button className="btn btn-primary" type='submit'>
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-save" viewBox="0 0 16 16">
                                    <path d="M2 1a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H9.5a1 1 0 0 0-1 1v7.293l2.646-2.647a.5.5 0 0 1 .708.708l-3.5 3.5a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L7.5 9.293V2a2 2 0 0 1 2-2H14a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2h2.5a.5.5 0 0 1 0 1H2z"/>
                                </svg>
                                <span className="ms-1">Save</span>
                            </button> */}
                            <button className="btn btn-primary" disabled={isFetchingData}  style={{minWidth : '5.5rem'}}>
                                { isFetchingData ? <div className="spinner-border spinner-border-sm" role="status" style={{borderWidth: '1px'}}></div> : <span>Save</span> }
                            </button>

                            <Link className="btn btn-outline-dark ms-3" to="/admin/jobs">
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="icon icon-chevron-left" viewBox="0 0 16 16">
                                    <path fillRule="evenodd" d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"/>
                                </svg>
                                <span className="ms-1">Back</span>
                            </Link>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
    );
}
export default ManageJob;